import React, { useState } from "react";
import {
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  TextField,
  Container,
  Typography,
  Grid,
  Button,
  Box,
} from "@mui/material";
import {
  FarmConfig,
  generateFarmBlueprintData,
} from "../blueprint/generateBlueprint";

interface ReactFarmConfig {
  farmConfig: FarmConfig;
}

const OutputBluePrint: React.FC<ReactFarmConfig> = ({ farmConfig }) => {
  const [bpData, setBpData] = useState<string>("");
  const handleGenerate = () => {
    const data = generateFarmBlueprintData(farmConfig);
    setBpData(data);
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" component="h1" gutterBottom>
        Button Grid and Text Frame
      </Typography>

      <Box sx={{ marginBottom: 2 }}>
        <Grid container spacing={2}>
          <Grid item xs={3}>
            <Button
              variant="contained"
              color="primary"
              fullWidth
              onClick={handleGenerate}
            >
              生成蓝图
            </Button>
          </Grid>
          <Grid item xs={3}>
            <Button variant="contained" color="secondary" fullWidth>
              Button 2
            </Button>
          </Grid>
          <Grid item xs={3}>
            <Button variant="contained" color="success" fullWidth>
              Button 3
            </Button>
          </Grid>
          <Grid item xs={3}>
            <Button variant="contained" color="error" fullWidth>
              Button 4
            </Button>
          </Grid>
        </Grid>
      </Box>

      <Box sx={{ border: "1px solid grey", padding: 2 }}>
        <TextField
          label="Enter Text"
          variant="outlined"
          fullWidth
          multiline
          rows={4}
          value={bpData}
          //   onChange={handleTextChange}
        />
      </Box>
    </Container>
  );
};

export default OutputBluePrint;
